@use 'base/mixins' as m;
@use 'base/variables' as v;


.dashboard {
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: v.$gris;
    &__header {
        background-color: v.$grisOscuro;
        padding: 2rem;
    }

    &__header-grid {
        display: flex;
        flex-direction: column;
        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }

    }
    &__logo {
      
        @include m.logo;
        @include m.texto-fluido(1rem, 3rem);
        text-decoration: none;
        
        &--texto {
            font-size: 2.5rem;
            cursor: pointer;
        }
    }
    &__nav {

    }

    &__form {
        display: flex;
        justify-content: center;
    }

    &__submit--logout {
        @include m.enlace;
    }

    &__grid {
        display: flex;
        flex: 1;
    }

    &__menu {

        padding: 1rem;
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
    }

    &__enlace {
        text-decoration: none;
        color: lighten(v.$grisOscuro, 40%);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 1rem;
        transition: 300ms background-color ease-in-out, 300ms color ease, 300ms border-radius ease;


        &:hover,
        &--actual {
            background-color: lighten(v.$grisOscuro, 20%);
            color: v.$blanco;
            border-radius: 1rem;
        }
    }

    &__icono {
        font-size: 2rem;
    }
    &__menu-texto {
        font-size: 1rem;
        text-transform: uppercase;
        font-weight: v.$bold;
        margin-top: 0.5rem;
        text-align: center;
    }

    &__sidebar {
        background-color: lighten(v.$grisOscuro, 5%);
        width: 15rem;
    }
    &__contenido {
        padding: 1rem 1rem 3rem 1rem;
        width: 100%;
        @include m.tablet {
            padding: 1rem 5rem 3rem 5rem;
        }
    }
}